import './App.css';
import List from './mycomponent/List'
import Search from './mycomponent/Search'
import useSemiPersistenceState from './Hooks/useSemiPersistenceState';

const item = [
  {
      title:"React",
      url:"https://reactjs.org/",
      author:"  jordan walki",
      num_comments:3,
      points:4,
      objectID:0,
  },
  {
      title:"Redux",
      url:"https://redux.js.org/",
      author:"  non abramov,andrew clark",
      num_comments:2,
      points:5,
      objectID:1,
  },
  {
    title:"cars",
  },
  {
    title:"grocery",
  },
  {
    title:"aircraft",
  },
  {
    title:"bucket",
  }
];

function App() {
  const[searchTerm,setSearchTerm] = useSemiPersistenceState("customSearchTerm","");
  const handleOnSearch = (e) =>{
    setSearchTerm(e.target.value);  
  };
  const filteredItem = item.filter((ite) => ite.title.toLowerCase().includes(searchTerm))
  return (
    <div>

      <Search  searchTerm = {searchTerm} onSearch={handleOnSearch}/>
       <h3>SEARCHING FOR:{searchTerm}</h3>
      <List item={filteredItem}/>
      </div>
  );
}

export default App;

